.footer {
  font-family: $ptmono;
  color: #1B2D37;
  padding: 65px 0 10px;

  @include tablets {
    padding: 21px 0 10px;
    font-size: 13px;
  }

  @include phones {
    padding: 40px 0 0;
  }
}

.footer__container {
  flex-direction: column;
  padding: 0 9px 0 30px;

  @include tablets {
    padding: 0 25px;
  }
  @include phones {
    padding: 0 15px;
  }
}

.footer__maincontent {
  display: flex;
  justify-content: space-between;
  margin-bottom: 55px;

  @include tablets {
    flex-wrap: wrap;
    margin-bottom: 36px;
  }

  @include phones {
    margin-bottom: 1vh;
  }
}

.footer__contacts {
  display: flex;
  flex-direction: column;
  width: 24%;

  @include tablets {
    width: 48%;
  }

  @include phones {
    width: 100%;
    position: relative;
  }
}

.footer__navigation {
  width: 32%;
  max-width: 445px;

  @include tablets {
    width: 50%;
  }

  @include phones {
    width: 100%;
    position: relative;
    margin-bottom: 24px;
  }
}

.footer__contacts:after,
.footer__navigation:after {
  @include phones {
    position: absolute;
    display: block;
    content: "";
    height: 2px;
    width: 1000%;
    background-color: rgba(#626962, 0.2);
    bottom: 0;
    left: -100%;
  }
}

.footer__links {
  position: relative;
  display: flex;
  flex-direction: column;

  @include tablets {
    width: 100%;
    flex-direction: row;
    align-items: center;
    padding-bottom: 25px;
    margin-bottom: 35px;
  }

  @include phones {
    flex-direction: column;
    padding-bottom: 60px;
    margin-bottom: 24px;
  }
}

.footer__links:after {
  @include tablets {
    position: absolute;
    display: block;
    content: "";
    height: 2px;
    width: 1000%;
    background-color: rgba(#626962, 0.2);
    bottom: 0;
    left: -100%;
  }
}

.footer__logo {
  margin-bottom: 53px;
  @include tablets {
    margin-bottom: 0;
    margin-right: 12%;
  }
  @include phones {
    margin-bottom: 29px;
    margin-right: 0;
  }
}

.footer__logo-image {
  width: 215px;
  @include phones {
    margin-bottom: 0;
  }
}

.footer__bottom {
  position: relative;
  display: flex;
  padding: 1.5vh 0 0;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  @include tablets {
    justify-content: space-between;
    padding: 20px 0 0;
  }

  @include phones {
    flex-direction: column;
    align-items: center;
  }
}

.footer__bottom:before {
  position: absolute;
  display: block;
  content: "";
  height: 2px;
  width: 1000%;
  background-color: rgba(#626962, 0.2);
  top: 0;
  left: -100%;

  @include phones {
    display: none;
  }
}

.footer__copyright {
  margin-right: 5.5%;
}

.footer__company-history {
  margin-right: 10.5%;
}

.footer__confidential {
  margin-right: 28.5%;

  @include tablets {
    margin-right: 0;
  }

  @include phones {
    margin-bottom: 3vh;
  }
}

.footer__socials {
  @include tablets {
    margin-left: auto;
  }
  @include phones {
    margin-right: auto;
  }
}